module.exports = {
  // 
  openExternalLink(link) {
    window.open(link);
  },

  // 
  openLink(link) {
    window.open(link, '_self');
  },

  //  news  tagText  tag 
  getNewsTagFromText(newsTagText) {
    switch (newsTagText) {
      case 'mirrors':
      case '':
        return 'mirrors';
      case 'iptv':
      case 'IPTV':
        return 'iptv';
      case 'repair':
      case '':
        return 'repair';
      case 'markdown':
      case '':
        return 'markdown';
      case 'catroom':
      case 'Cat Room':
        return 'catroom';
      case 'nuofficial':
      case '':
        return 'nuofficial';
      default:
        return '';
    }
  },

  //  news  tag 
  getNewsTagText(newsTag) {
    switch (newsTag) {
      case 'mirrors':
      case '':
        return '';
      case 'iptv':
      case 'IPTV':
        return 'IPTV';
      case 'repair':
      case '':
        return '';
      case 'markdown':
      case '':
        return '';
      case 'catroom':
      case 'Cat Room':
        return 'Cat Room';
      case 'nuofficial':
      case '':
        return '';
      default:
        return '';
    }
  },

  //  news  accentText  accent 
  getNewsAccentFromText(accentText) {
    switch (accentText) {
      case 'info':
      case '':
        return 'info';
      case 'accent':
      case '':
        return 'accent';
      case 'warning':
      case '':
        return 'warning';
      case 'error':
      case '':
        return 'error';
      default:
        return 'info';
    }
  },

  //  news  accent 
  getNewsAccentText(accent) {
    switch (accent) {
      case 'info':
      case '':
        return '';
      case 'accent':
      case '':
        return '';
      case 'warning':
      case '':
        return '';
      case 'error':
      case '':
        return '';
      default:
        return '';
    }
  },

  getNewsAccentColorClass(accent) {
    switch (accent) {
      case 'info':
      case '':
        return 'color-info';
      case 'accent':
      case '':
        return 'color-accent';
      case 'warning':
      case '':
        return 'color-warning';
      case 'error':
      case '':
        return 'color-error';
      default:
        return 'color-info';
    }
  },

  //  localStorage 
  removeLocalStorageUserInfo() {
    localStorage.removeItem('token');
    localStorage.removeItem('tokenAdmin');
    localStorage.removeItem('githubId');
    localStorage.removeItem('username');
    localStorage.removeItem('userBio');
  },
};
